import React from "react";
import visualVideo from '../videos/visual.mp4'; // MP4  

export default function Visual() {
    return (
        <div className="relative h-[500px] lg:min-h-screen">
            <div className="absolute top-0 left-0 w-full h-full bg-zinc-900 opacity-50 z-10"></div> 
            <div className="absolute lg:h-screen h-[500px] flex flex-col justify-center items-center w-full z-10 p-8 text-center">
                <p className="font-extrabold text-white text-4xl lg:text-4vw pt-[10%] lg:pt-0"><br className="lg:hidden" /> Quick Survey<br />
  <br className="lg:hidden" /> </p>
<p className="font-semibold text-white text-base pt-2 lg:text-1.8vw lg:pt-8 brnone">         .<br />
       .</p>
            </div>
        <video 
            className="absolute top-0 left-0 w-full h-full object-cover"
            autoPlay 
            muted 
            loop
        >
            <source src={visualVideo} type="video/mp4" />
            Your browser does not support the video tag.
        </video>
    </div>
    );
}
