
import React, {  useState } from 'react';
import Footer from '../component/footer';
import axios from 'axios';

const HomePage = () => {


  const inputClass = 'rounded-md bg-zinc-100 p-3 w-full';
  const [fileInputs, setFileInputs] = useState([1]);
  const [files, setFiles] = useState([]);

  const addFileInput = () => {
    setFileInputs([...fileInputs, fileInputs.length + 1]);
    setFiles([...files, null]); //      null    .
  };

  const [formData, setFormData] = useState({
    username: '',
    password: '',
    company: '', //  
  });



  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData(prevState => ({ ...prevState, [name]: value }));
  };

  const handleFileChange = (event, index) => {
    const selectedFile = event.target.files[0]; //   
    const newFiles = [...files]; //    
    newFiles[index] = selectedFile; //      
    setFiles(newFiles); //   
  };

  const handleSubmit = async (event) => {
    event.preventDefault();

    const formDataToSend = new FormData();

    console.log(formData)
    console.log(files)

    //   
    formDataToSend.append('username', formData.username);
    formDataToSend.append('password', formData.password);
    formDataToSend.append('company', formData.company);

    //  
    files.forEach((file, index) => {
      formDataToSend.append(`file${index + 1}`, file);
    });

    try {
      const response = await axios.post('http://example.com/api/resource', formDataToSend, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });
      console.log(':', response.data);
    } catch (error) {
      console.error(':', error);
    }
  };


  return (
    <div className="break-keep">
      <div className='svisual_02 flex justify-center text-white font-bold h-40 text-2xl pt-20 lg:pt-40 lg:h-96 lg:text-6xl'></div>
      <div className='flex flex-col items-center justify-center lg:p-20 p-8 pt-10 pb-10'>

        <div className='lg:w-8/12 text-center'>
          <p className='font-bold text-4xl lg:text-3.5vw text-center'><br className='lg:hidden' /><span className='text-c1'>Quick Survey</span><br />  ?</p>
          <p className='lg:text-1vw text-zinc-500 lg:pt-6 lg:pb-8 text-center leading-tight pt-4 pb-4'>      .</p>

          <div className='rounded-xl bg-zinc-50 p-8 mb-8 text-left'>
            <p className='flex items-center font-bold text-md pb-4'><input type="checkbox" className='bg-c1 text-white w-6 h-6 mr-2 rounded-md'></input>     ()</p>
            <p className='lg:h-auto h-[100px] overflow-auto text-zinc-500 lg:text-1vw'>   ,  .      .<br />
: , , , , <br />
:    <br />
   : 1<br />
       . ,           .</p>
          </div>
          <p className='font-bold text-left w-full pb-4'><span className='text-c1'>*</span>  .</p>
          <form onSubmit={handleSubmit}>
          <div className='rounded-xl border border-c-zinc-200 p-8 lg:p-10 text-left'>
              <ul className='grid grid-cols-2 gap-8 font-bold'>
                <li className='lg:col-span-1 col-span-2'><p className='pb-2'>(  ,  )</p> <input type="text" name="company" value={formData.company} onChange={handleChange} className={inputClass} /></li>
                <li className='lg:col-span-1 col-span-2'><p className='pb-2'>(,, )</p> <input type="text" className={inputClass} /></li>
                <li className='lg:col-span-1 col-span-2'><p className='pb-2'><span className='text-c1 pr-2'>*</span></p> <input type="text" className={inputClass} /></li>
                <li className='lg:col-span-1 col-span-2'><p className='pb-2'><span className='text-c1 pr-2'>*</span></p> <input type="text" className={inputClass} /></li>
                <li className='col-span-2'><p className='pb-2'></p> <input type="text" className={inputClass} /></li>
                <li className='col-span-2'><p className='pb-2'></p> <input type="text" className='rounded-md bg-zinc-100 p-3 w-full h-28' /></li>
                <li className='col-span-2'><p className='pb-2'></p> <input type="text" className={inputClass} /></li>
                <li className='col-span-2'><p className='pb-2'><span className='text-c1 pr-2'>*</span>   </p> <input type="text" className='rounded-md bg-zinc-100 p-3 w-full h-28' /></li>
                <li className='col-span-2'><p className='pb-2'> (,  ) <span className='text-c1 text-sm block lg:inline'>* ()         .</span></p> 
                {fileInputs.map((input, index) => (
                    <div key={index}>
                      <input
                        type="file"
                        className={`${inputClass} mb-2`}
                        onChange={(event) => handleFileChange(event, index)} //  
                      />
                      {index === fileInputs.length - 1 && ( //        
                        <p
                          className='font-bold text-zinc-900 text-right pt-4 text-sm pr-4 cursor-pointer'
                          onClick={addFileInput}
                        > + </p>
                      )}
                    </div>
                  ))}

                 </li>
              </ul>
          </div>

          <button type="submit" className='bg-c1 p-4 pl-20 pr-20 font-bold text-white text-xl rounded-full mt-6'></button>
   

          </form>
        </div>
      </div>

      <Footer />
    </div>
  );
};

export default HomePage;
